import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SharedModule } from '@shared/shared.module';
import { PageLeaveGuard } from '@core/routing-guards';

import { AboutYouComponent } from './about-you.component';

const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    component: AboutYouComponent,
    canDeactivate: [ PageLeaveGuard ]
  }];

@NgModule({
  imports: [SharedModule, RouterModule.forChild(routes)],
  declarations: [AboutYouComponent]
})
export class AboutYouModule {}
