import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';

import { combineLatest, Observable, of } from 'rxjs';
import { concatMap, first, map  } from 'rxjs/operators';

import { areDifferent } from '@core/utils';
import { CompanyService, UserService } from '@entity';
import { Company, CompanyType, User } from '@model';
import { WellKnownRoutes } from '@core';
import { ChangeSetItem } from 'ngrx-data';
import { changeItemFactory as cif } from '@utils';
import { environment } from '@environment';

export interface AboutYouVm {
  company: Company;
  user: User;
}

@Component({
  selector: 'sp-about-you',
  templateUrl: './about-you.component.html'
})
export class AboutYouComponent implements OnInit {

  next: string = WellKnownRoutes.CompanyInfo;

  @ViewChild('form') form = { invalid: true } as NgForm;

  /** Current view model */
  private vm: AboutYouVm;

  /** Terminating snapshot observable of the latest Company & User.*/
  private modelData$: Observable<[Company, User]>;

  /** Terminating snapshot observable of the latest Company & User as a VM.*/
  vm$: Observable<AboutYouVm>;

  constructor(
    private companyService: CompanyService,
    private userService: UserService
  ) {}

  ngOnInit(): void {
    this.modelData$ = combineLatest(
      this.companyService.currentCompany$,
      this.userService.currentUser$)
      .pipe(first());

    this.vm$ = this.modelData$.pipe(
      map(([company, user]) => {
        // clone the current data so can mutate with ngModel
        // hold a local reference of the VM for later save
        return this.vm = {
          company: { ...company },
          user: { ...user },
        };
      })
    );
  }

  canLeaveAfterSave() {
    return this.save();
  }

  private save() {
    if (!this.vm || this.form.invalid) {
      return false; // don't save if form is invalid or no VM
    }

    // tslint:disable-next-line:prefer-const
    let {company: newCompany, user: newUser} = this.vm;
    const saveUrl = environment.apiRoot + '/save/companyuser';

    // Get fresh Company and User and save each if it was changed here.
    return this.modelData$.pipe(
      concatMap(([oldCompany, oldUser]) => {
        const changes: ChangeSetItem[] = [];

        const isHousehold = newCompany.isHousehold;
        if (isHousehold !== oldCompany.isHousehold) {

          if (isHousehold) {
            oldCompany.isAgricultural = false;
          }
          const companyType = isHousehold ? CompanyType.Household : null;
          newCompany = { ...oldCompany, companyType, isHousehold };
          changes.push(cif.update('Company', newCompany));
        }

        if (areDifferent(newUser, oldUser, 'User')) {
          changes.push(cif.update('User', newUser));
        }

        if (changes.length) {
          return this.companyService.saveEntities({ changes }, saveUrl).pipe(map((_) => true));
        } else {
          return of(true);
        }

      })
    );
  }
}
